class Attendance extends React.Component {
    constructor(props) {
        super(props);
        const { t } = this.props;
        this.fieldVariables = {
            periodDetails: []
        }
        this.state = {
            students:'', periodObj: "", periodStartTime: "07:30", periodEndTime: "08:30", successMessage: '', isSuccess: false, students: [], errorMessage: '', isError: false
        };
    }

    async componentDidMount() {
        let response = await this.props.authenticatedApiCall('get', '/api/teacherservice/getmystudents', null);
        let getdailyattendance = await this.props.authenticatedApiCall('get', '/api/teacherservice/getdailyattendance', null);
        if (response.data.status == 1) {
            response.data.statusDescription.forEach((item) => {
                item.name = item.firstname + " " + item.lastname;
                item.first = {
                    userid: item.userid, classid:item.classid, section:item.section 
                }

            });
            this.setState({
                students: response.data.statusDescription, isLoading: false
            })
        }
    }
    handelSubmit = async (values) => {
        console.log('values',values)

            let response = await this.props.authenticatedApiCall('post', '/api/teacherservice/savedailyattendance', values);
        if(response.data.status === 1){
            alert('success')
        }else{
            alert('error')
        }
    }
    
    render() {
        const { classes } = this.props;
        return (
            <Formik onSubmit={this.handleSubmit} validationSchema={this.yupSchema} initialValues={{ attendanceObj:[], arrayObj:[] }}>
                {(props) => (
                    <Form>
                          <AttendanceUI studentsList={this.state.students}/>
                        <Button type="submit">Save</Button>
                    </Form>
                )}
            </Formik>
        );
    }
}
