class AttendanceUI extends React.Component {
    state = {
        attendanceArray: 1, periodObj: "", periodStartTime: "07:30", periodEndTime: "08:30", successMessage: '', isSuccess: false, students: [], errorMessage: '', isError: false
    };


    backDashboard = () => {
        this.setState({ isError: false, isSuccess: false })
    }
    handleChangeCheckBox = (item, index) => {
        this.props.formik.setFieldValue(`arrayObj[${index}]`, item, false)
    }

    render() {
        console.log('attendance', this.props.formik.values.arrayObj)
        const { classes, studentId } = this.props;
        return (
            <>
                {this.props.studentsList.map((item, index) =>
                    <><Typography>{item.firstname}</Typography>
                    <Field
                        name="attendanceObj"
                        component={SimpleCheckBox}
                        // checked={index == 2 ? true : false}
                        onClick={(e) => this.handleChangeCheckBox(item, index, e)}
                    /><br/></>)}
            </>
        );
    }
}
