import React from 'react';
import AuthenticatedPage from "../AuthenticatedPage";
import { withStyles, Button } from '@material-ui/core';
import GridContainer from "../../components/Grid/GridContainer.jsx";
import GridItem from "../../components/Grid/GridItem.jsx";
import MuiThemeDataTable from '../../components/MuiThemeDataTable';
import ErrorDialog from '../../components/ErrorDialog';

const styles = theme => ({
    root: {
        marginTop: theme.spacing.unit * 8,
        ...theme.mixins.gutters(),
        paddingTop: theme.spacing.unit * 1,
        paddingBottom: theme.spacing.unit * 1,
    },
    OkButton: { backgroundColor: theme.palette.button.okButtonBackground, borderRadius: "15px", fontSize: "12px", color: "#fff", width: "100px", textAlign: "right", '&:hover': { background: theme.palette.button.okButtonHover } }
});

class ResultList extends React.Component {
    state = {
        Results: [], isError: false, errorMessage: ''
    };

    tableheads1 = [
        {
            name: "studentid",
            label: "SR. No.",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "hindi",
            label: "Hindi",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "hindiobtainmarks",
            label: "HOM",
            options: {
                filter: true,
                sort: true,
                searchable: true,
            }
        },

        {
            name: "english",
            label: "English",
            options: {
                filter: true,
                sort: true,
                searchable: false
            }
        },
        {
            name: "englishobtainmarks",
            label: "EOM",
            options: {
                filter: false,
                sort: true,
                searchable: true
            }
        },
        {
            name: "math",
            label: "Math",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "mathobtainmarks",
            label: "MOM",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "science",
            label: "Science",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "scienceobtainmarks",
            label: "SOM",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "history",
            label: "History",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "historyobtainmarks",
            label: "HOM",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "physics",
            label: "Physics",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        },
        {
            name: "physcisobtainmarks",
            label: "POM",
            options: {
                filter: true,
                sort: true,
                searchable: true
            }
        }
    ];

    async componentDidMount() {
        let response = await this.props.authenticatedApiCall('get', '/api/teacherservice/getstudentsresult', null);
        if (response.data.status == 1) {
            this.setState({ Results: response.data.statusDescription })
        } else if (response.data.status == 0) {
            this.setState({ errorMessage: response.data.statusDescription, isError: true })
        }
    }

    backDashboard = () => {
        this.props.history.push(`./studentlist`)
        this.setState({ isError: false })
    }
    render() {
        const { classes } = this.props;
        const OkButton = [<Button className={classes.OkButton} onClick={this.backDashboard}>Ok</Button>]
        return (
            <div className={classes.root}>
                <GridContainer justify="center">
                    <GridItem md={12}>
                        <br></br>
                        <MuiThemeDataTable title={'Students Result'} rows={this.state.Results} columns={this.tableheads1} />
                        {(this.state.isError ? <ErrorDialog successButton={OkButton} HeaderText={this.state.errorMessage} dismiss={this.backDashboard} /> : "")}
                    </GridItem>
                </GridContainer>
            </div>
        );
    }
}

export default withStyles(styles)(AuthenticatedPage(["Teacher"])(ResultList));
